import { Button } from '@material-ui/core';
import React from 'react';
import img9 from './../images/img9.jpg'
import TableRailWire from '../components/TableRailWire'
import TandC from '../components/TandC';

const Railwire = () => {
    return (
        <>
        <div className="container-fluid main_container">
            <div className="row justify-content-center h-100">
                <div className="col-sm-10">
                    <div className="row row_100">
                        <div className="col-sm-6 order-2 order-lg-1 body_left">
                            <h1>About <strong className="brand-name">RailWire </strong>broadband</h1>
                            <div className="sub-title">RailWire is community based broadband internet service provided by RailTel in partnership with more than 5000 communities in India. Incepted in 2008, RailWire broadband is delivered all over India through 59,500+ km optic fiber network running through exclusive right of way along railway tracks.</div>
                            <Button variant="outlined" className="round_button" color="primary">
                                Read More
                            </Button>
                        </div>
                        <div className="col-sm-6  order-1 order-lg-2 body_right ">
                            <img src={img9} alt="" className="right_image" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div className="container-fluid">
            <div className="row justify-content-center h-75">
                <div className="col-sm-10 body_left">
                        <h1>Broadband <strong className="brand-name">Plans</strong> with Unlimited-Data.</h1>
                        <div className="sub-title">Best plans for you! </div>
                        <br />
                        <TableRailWire/>
                        <br />
                        <TandC url="https://www.railwireinternet.com/terms"/>
                        <br />
                        <br />
                        <br />
                        <br />
                </div>
            </div>
        </div>
        </>
    )
}

export default Railwire;