import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Home from './pages/Home'
import Product from './pages/Product'
import Services from './pages/Services'
import About from './pages/About'
import Quickpay from './pages/Quickpay'
import Error404 from './pages/Error404';
import Header from './components/Header';
import './index.css';
import Footer from './components/Footer';
import Bottomnav from './components/Bottomnav';
import Map from './components/Map';
import Railwire from './pages/Railwire';
import Bsnl from './pages/Bsnl';
import JioFiber from './pages/JioFiber';



ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Header/>




      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/product" component={Product} />
        <Route exact path="/services" component={Services} />
        <Route exact path="/about" component={About} />
        <Route exact path="/quickpay" component={Quickpay} />
        <Route exact path="/product/railwire" component={Railwire} />
        <Route exact path="/product/bharatfiber" component={Bsnl} />
        <Route exact path="/product/jiofiber" component={JioFiber} />
        <Route component={Error404} />
      </Switch>




      <Footer/>
      <Map/>
      <Bottomnav/>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);
