import React from 'react'
import img13 from './../images/img13.png'


const Quickpay = () => {
    return (
        <div className="container-fluid main_container">
        <div className="row justify-content-center h-100">
            <div className="col-sm-10">
                <div className="row row_100">
                    <div className="col-sm-6 col-0  order-1 order-lg-1 body_right ">
                        <img src={img13} alt="" className="right_image" />
                    </div>
                    <div className="col-sm-6 col-12 order-2 order-lg-2 body_left">
                        <h4>Pay Ditect through <strong className="brand-name">QuickPay</strong></h4>
                        <div className="sub-title">Enter 10 degit valid phone number</div>
                        <form action="https://billing.isphub.in/DirectPay/details.php" method="post">
                            <div className="input-group mb-3 btn_grp">
                                <span className="input-group-text">+91</span>
                                
                                <input required maxLength="10" name="phone" size="10" type="phone" className="form-control p-2" placeholder="XXXXX XXXXX" aria-label="Recipient's username" aria-describedby="button-addon2"/>
                                
                                <button className="btn btn-primary" type="submit" id="button-addon2">Search</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Quickpay;