import { Link, Button } from '@material-ui/core';
import React from 'react'
import './footer.css'
import img2 from './../images/img2.png'
import Listitem from './Listitem'
import PolicyTwoToneIcon from '@material-ui/icons/PolicyTwoTone';
import RoomServiceTwoToneIcon from '@material-ui/icons/RoomServiceTwoTone';
import VerifiedUserTwoToneIcon from '@material-ui/icons/VerifiedUserTwoTone';
import CancelPresentationTwoToneIcon from '@material-ui/icons/CancelPresentationTwoTone';
import InfoTwoToneIcon from '@material-ui/icons/InfoTwoTone';
import ContactsTwoToneIcon from '@material-ui/icons/ContactsTwoTone';


const Footer = () => {
    return (
        <div className="container-fluid cont_footer">
            <div className="row justify-content-center">
                <div className="col-sm-10 footer_cont p-0">
                    <nav className="navbar navbar-dark bottom_nav p-0 mt-4">
                        <div className="container-fluid">
                            <Link className="navbar-brand" to="/">ISP<b className="hub">hub</b></Link>
                            <form className="d-flex">
                            <input className="form-control me-2" type="search" placeholder="example@email.com" aria-label="Search"/>
                            <Button variant="contained" className="SubscribeBtn" color="primary" disableElevation>
                                Subscribe
                            </Button>
                            </form>
                        </div>
                    </nav>
                    <div className="row w-100">
                    <div className="col-sm-4 p-4">
                            <h5>Know More</h5>
                            <ul className="list-group list-group-flush">
                                <Listitem icon={<PolicyTwoToneIcon fontSize="small"/>} title="Acceptable Usage Policy"/>
                                <Listitem icon={<RoomServiceTwoToneIcon fontSize="small"/>} title="Terms of Service"/>
                                <Listitem icon={<VerifiedUserTwoToneIcon fontSize="small"/>} title="Privacy Agreement"/>
                                <Listitem icon={<CancelPresentationTwoToneIcon fontSize="small"/>} title="Cancellation / Refund Policy"/>
                            </ul>
                            <br />
                        </div>
                        <div className="col-sm-4 p-4">
                            <h5>Quick Links</h5>
                            <ul className="list-group list-group-flush">
                                <Listitem icon={<InfoTwoToneIcon fontSize="small"/>} title="About Company"/>
                                <Listitem icon={<ContactsTwoToneIcon fontSize="small"/>} title="Contact Info"/>
                            </ul>
                            <br />
                        </div>
                        <div className="col-sm-4 p-4">
                            <h5>Secure Payment by</h5>
                            <img src={img2} alt="" className="paymentimage" />
                            <br />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer;