import { IconButton, Button} from '@material-ui/core';
import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import "./../../node_modules/bootstrap/dist/css/bootstrap.min.css"
import "./../../node_modules/bootstrap/dist/js/bootstrap.bundle.js"
import "./Header.css"
import MenuIcon from '@material-ui/icons/Menu';





const Header = () => {
    return (
     <>
        <nav className="navbar navbarTop boxShadow navbar-expand-lg navbar-light pt-0 pb-0 fixed-top">
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">ISP<b className="hub">hub</b></Link>
                <div className="ham_burger_icon">
                    <IconButton type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <MenuIcon/>
                    </IconButton>
                </div>
                <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                        <NavLink exact activeClassName="isActive_inNav" className="nav-link nav_option" aria-current="page" to="/">Home</NavLink>
                        </li>
                        <li className="nav-item dropdown">
                        <NavLink className="nav-link nav_option" to="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                            Product
                        </NavLink>
                        <ul className="dropdown-menu boxShadow" aria-labelledby="navbarDropdown">
                            <li><Link className="dropdown-item" to="/product/railwire">Railwire Broadband</Link></li>
                            <li><Link className="dropdown-item" to="/product/bharatfiber">BSNL Bharat Fiber</Link></li>
                            <li><Link className="dropdown-item" to="/product/jiofiber">Jio Fiber</Link></li>
                        </ul>
                        </li>
                        <li className="nav-item">
                        <NavLink exact activeClassName="isActive_inNav" className="nav-link nav_option" to="/support" >Support</NavLink>
                        </li>
                        <li className="nav-item">
                        <NavLink exact activeClassName="isActive_inNav" className="nav-link nav_option" to="/about" >About</NavLink>
                        </li>
                        <li className="nav-item">
                        <NavLink exact activeClassName="isActive_inNav" className="nav-link nav_option" to="/quickpay" >QuickPay</NavLink>
                        </li>
                    </ul>
                    <div className="right_nav btn-group">
                        <Button variant="contained" className="quickpay" color="primary" disableElevation>
                            Login
                        </Button>
                    </div>
                </div>
            </div>
        </nav>
     </>
    )
}
export default Header;