import React from 'react';

const TandC = (props) => {
    return(
        <>
        <h4>Notes:</h4>
        <p>- GST additional on applicable components</p>
        <p>- Customer will need to activate Amazon Prime and Netflix subscription once through RailWire to avail the benefits</p>
        <p>- All plans have symmetric upload and download speed</p>
        <a className="text-primary cursor-pointer" href={props.url}>-Terms and Conditions</a>
        
        </>
    )
}

export default TandC;