import React from 'react'
import "./../css/home.css"
import {Button} from '@material-ui/core'
import E404 from './../images/404.png'
import { useLocation, Link } from 'react-router-dom'

const Error404 = () => {
    return (
        <>
         <div className="container-fluid main_container">
            <div className="row justify-content-center h-100">
                <div className="col-sm-10">
                    <div className="row row_100">
                        <div className="col-sm-6 order-2 order-lg-1 body_left">
                            <h1>Opps! Something went wrong <strong className="brand-name">404</strong> Page Not Found!</h1>
                            <div className="sub-title">...{useLocation().pathname} page not found!</div>
                            <Link to="/">
                                <Button variant="outlined" className="round_button" color="primary">
                                    GO HOME Page
                                </Button>
                            </Link>
                        </div>
                        <div className="col-sm-6  order-1 order-lg-2 body_right ">
                            <img src={E404} alt="" className="right_image" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}

export default Error404;